.section.footer_section {
  padding-top: 0;
  padding-bottom: 0;
}

.container.footer_container {
  border: 1.5px solid var(--dark-theme-componemt);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  justify-content: center;
  align-items: center;
  height: 72px;
  padding-top: 13px;
  padding-bottom: 14px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .footer_container_shop {
    background-color: rgba(0, 0, 0, 0);
  }
}

.shop {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.footer_wrapper {
  text-align: center;
  color: var(--dark-theme);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.125;
}

@media screen and (min-width: 768px) {
  .footer_wrapper {
    font-size: 16px;
    line-height: 1.2;
  }
}

.footer_icon {
  fill: var(--color-blue-component);
}

.footer_icon:hover, .footer_icon:focus {
  fill: var(--color-yellow-componemt);
}

.footer_text_left {
  margin-bottom: 10px;
}

@media screen and (min-width: 768px) {
  .footer_text_left {
    margin-bottom: 0;
    display: inline-block;
  }
}

.footer_text_right {
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: inline-flex;
}

.heart_beats {
  -webkit-animation: 3s infinite heart-beats;
  animation: 2s infinite heart-beats;
}

@keyframes heart-beats {
  7% {
    transform: scale(1.2);
  }

  12% {
    transform: scale(1.1);
  }

  20% {
    transform: scale(1.3);
  }

  60% {
    transform: scale(1);
  }

  100% {
    transform: scale(.99);
  }
}

@-webkit-keyframes heart-beats {
  0% {
    -webkit-transform: scale(1);
  }

  7% {
    -webkit-transform: scale(1.2);
  }

  12% {
    -webkit-transform: scale(1.1);
  }

  20% {
    -webkit-transform: scale(1.3);
  }

  60% {
    -webkit-transform: scale(1);
  }

  100% {
    -webkit-transform: scale(.99);
  }
}

